.app {
    text-align: center;
    background-color: #7FB036;
    background: -webkit-linear-gradient(315deg, #7FB036 0%, #7FB036 30%, #d8ff5c 100%) fixed;
    background: linear-gradient(135deg, #7FB036 0%, #7FB036 30%, #d8ff5c 100%) fixed;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    line-height: 22px;
    padding: 20px 0;
}

.app-content {
    color: #343434;
    width: 100%;
    max-width: 620px;
    background-color: #fff;
    padding: 20px;
    margin-top: 20px;
    border-radius: 10px;
    box-shadow: 2px 2px 10px rgba(0,0,0,.03)
}

.app-content-section {
    padding: 20px;
    margin-top: 20px;
}

.app-content-profile {
    margin-top: 60px;
    padding: 0 20px 30px;
}

.app-profilepic {
    margin-top: -60px;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.app-profilepic > div {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.app-content .app-article {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.app-content .app-article h1 {
    font-size: 20px;
    line-height: 1.2;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 700;
    margin: 0;
    margin-bottom: 8px;
    color: #333;
}

.app-content .app-article h3 {
    margin: 0;
    padding: 0;
    border: 0;
    font-weight: inherit;
    font-style: inherit;
    font-size: 100%;
    font-family: inherit;
    vertical-align: baseline;
}

.app-content .app-article > * {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    max-width: 400px;
}

.app-logo {
    height: 40vmin;
}

.app-link {
    color: #09d3ac;
}

.app-content .social-links ul {
    padding: 0;
    margin: 0;
    margin-top: 20px;
    list-style: none;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
}

.app-content .social-links li {
    padding: 0;
    margin: 12.5px;
    width: 36px;
    height: 36px;
    font-size: 30px;
    line-height: 36px;
    cursor: pointer;
    display: block;
    -webkit-font-smoothing: antialiased;
    color: #999;
}

.app-content .social-links a {
    opacity: .5;
    transition: .2s;
}

.app-content .social-links a:hover {
    opacity: 1;
}

.app-content .bio {
    text-align: justify;
}

.app-portfolio h4 {
    color: #787898;
    margin: 0 0 10px;
}

.app-portfolio a {
    display: inline-block;
    padding: 0 8px;
}
